.logo {
    color: rgb(14, 12, 12);
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 35px;
}
.pages ul li{
    font-size: 20px;
    justify-content: center;
}
nav {
    background-color: #3286f3;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 10px;
  }
  
  li {
    display: inline;
    margin-right: 20px;
  }
  
  li:last-child {
    margin-right: 0;
  }
  
  a {
    color: #fff;
    text-decoration: none;
  }
  
  a:hover {
    color: #131011;
  }